<template>
  <section class="hero">
    <div class="hero-body">
      <p class="title">
        Peikai Tan
      </p>
      <p class="subtitle">
        PSI Table <br>
        Last updated: {{ time }}
      </p>

    </div>
  </section>
  <div class="container">
    <table class="table is-fullwidth is-striped is-bordered is-hoverable">
      <thead>
        <tr>
          <th></th>
          <th>National</th>
          <th>Central</th>
          <th>North</th>
          <th>East</th>
          <th>South</th>
          <th>West</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(region, key) in psi_values" v-bind:key="key">
          <td>{{ key }}</td>
          <td> {{ region["national"] }}</td>
          <td> {{ region["central"] }}</td>
          <td> {{ region["north"] }}</td>
          <td> {{ region["east"] }}</td>
          <td> {{ region["south"] }}</td>
          <td> {{ region["west"] }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
const axios = require('axios').default;

export default {
  name: 'App',
  data() {
    return {
      psi_values: {},
      time: null
    }
  },
  mounted() {
    axios.get('https://api.data.gov.sg/v1/environment/psi').then(response => (this.ConsumeData(response)));
  },
  methods: {
    ConsumeData(response) {
      this.psi_values = response.data.items[0].readings;

      let today = new Date()
      this.time = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate() + '   ' + today.getHours() + ":" + today.getMinutes();
    }
  }
}


</script>

